<template>
	<view class="page commonPageTitle">
		<view class="collectTitle">
			<view class="goBackBtn" @click="goBack(1)"></view>
			<view class="title">
				转换记录
			</view>
		</view>
		<view class="noTranIn">
			<image src="http://beifa-mall.oss-cn-shanghai.aliyuncs.com/appletsImages/card_paysuccess@2x.png" mode=""></image>
			<view>
				暂无记录！
			</view>
		</view>
	</view>
</template>

<script>
	import memberApi from '@/api/member/index.js'
	export default{
		data(){
			return{
				tranIn:{	//转换记录
					
				}
			}
		},
		methods:{
			convertLog(params){
				memberApi.convertLog(params).then(res=>{
					if(res.data.code==200){
						this.tranIn = res.data.data
					}else{
						uni.showToast({
							title:res.data.message,
							icon:"none"
						})
					}
				})
			}
		},
		onReachBottom(){
			
		},
		onShow() {
			this.convertLog(0)
		}
	}
</script>

<style scoped>
	.commonPageTitle {
		padding-top: 88rpx;
	}
	.collectTitle {
		position: fixed;
		width: 100%;
		top: 0;
		left: 0;
		height: 88rpx;
		z-index: 1;
		display: flex;
		background-color: #fff;
		justify-content: space-between;
	}
	
	.goBackBtn {
		width: 88rpx;
		height: 88rpx;
		background: url(http://beifa-mall.oss-cn-shanghai.aliyuncs.com/appletsImages/page_1@2x.png) no-repeat 50%;
		background-size: 16rpx 30rpx;
	}
	
	.title {
		width: calc(100% - 180rpx);
		height: 88rpx;
		line-height: 88rpx;
		position: absolute;
		left: 0;
		right: 0;
		margin: auto;
		text-align: center;
		font-size: 36rpx;
	}
	.noTranIn{
		width: 100%;
		height: calc(100vh - 88rpx);
		text-align: center;
	}
	.noTranIn image{
		width: 300rpx;
		height: 300rpx;
		margin-top: 50%;
	}
</style>
